import { BIconTrashFill } from 'bootstrap-vue'

export default {

  components: {
    BIconTrashFill
  },
  methods: {
    deleteItem(select, i) {
      this.$store.state.selected.splice(i, 1)
      this.$store.state.sum -= +select.price
    }
  },

}
