export default {

  async fetch() {
    this.items = await fetch(
      'https://my-json-server.typicode.com/Abbos-coder/shopify/products'
    ).then(response => response.json())
  },

  data() {
    return {
      items: [],
      selected: [],
      cost: [],
      sum: 0,
      current: 0,

    }
  },
  methods: {
    addToCard(item) {
      this.$store.state.cost.push(item.price)
      this.$store.state.selected.push(item)
      this.$store.state.sum += +item.price
      this.$store.state.items = this.items
    },
    makeToast(variant = null) {
      this.$bvToast.toast('Product adding to cart', {
        title: `Selected Product`,
        variant: variant,
        solid: true,
        autoHideDelay: 1200
      })
    }
  },

}
